<template>
  <div>
    <!-- Header -->
    <app-header headerClass="header--white"></app-header>

    <!-- Single Product -->
    <div v-if="product">
      <div class="single-product">
        <div class="container">
          <!-- Product Image -->
        <div class="single-product__image">
          <div class="swiper-container gallery-top">
            <div class="swiper-wrapper">
              <div class="swiper-slide">
                <img :src="product[0].images[0]">
              </div>
              <div class="swiper-slide">
                <img :src="product[0].images[1]">
              </div>
              <div class="swiper-slide">
                <img :src="product[0].images[2]">
              </div>
            </div>
            <!-- Add Scrollbar -->
            <div class="swiper-scrollbar"></div>
          </div>
          <div class="swiper-container gallery-thumbs">
            <div class="swiper-wrapper">
              <div class="swiper-slide">
                <img :src="product[0].images[0]">
              </div>
              <div class="swiper-slide">
                <img :src="product[0].images[1]">
              </div>
              <div class="swiper-slide">
                <img :src="product[0].images[2]">
              </div>
            </div>
          </div>
        </div>
          <!-- Product Info -->
          <div class="single-product__info">
            <!-- Title & Price -->
            <div class="single-product__main-info">
              <h1 class="single-product__title">{{ product[0].name }}</h1>
              <p class="single-product__price">
                ${{ product[0].price }}
              </p>
            </div>
            <!-- Color -->
            <div class="single-product__color">
              <p>Color:</p>
              <div class="single-product__color-box">
                <span class="single-product__color-hex" :class="'bg-' + product[0].color"></span>
              </div>
            </div>
            <!-- Add To Cart -->
            <div class="single-product__add-to-cart">
              <!-- Quantity -->
              <p>Quantity:</p>
              <div class="d-flex">
                <div class="quantity">
                  <div class="quantity__minus" @click="changeQuantity">-</div>
                  <input class="quantity__input" type="number" v-model="quantity" disabled>
                  <div class="quantity__plus" @click="changeQuantity">+</div>
                </div>
                <button class="btn btn--orange" @click="addToCart">Add To Cart</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Product Description -->
    <div class="single-product__description">
      <div class="container">
        <div class="btn btn--bordered">Description</div>
        <!-- Product Description Boxes -->
        <div class="single-product__description-boxes">
          <!-- Product Description Box -->
          <div class="single-product__description-box">
            <img src="img/icons/description.svg" alt="description-icon" class="single-product__description-img">
            <h3 class="single-product__description-heading">Details and product description</h3>
            <p class="single-product__description-text">T-shirts in the summer line with a colorful print. Made of
              jersey
              cotton. T-shirt fits perfectly with jeans, pants or shorts.</p>
          </div>
          <!-- Product Material Box -->
          <div class="single-product__description-box">
            <img src="img/icons/material.svg" alt="material-icon" class="single-product__description-img">
            <h3 class="single-product__description-heading">Material(s) and care</h3>
            <p class="single-product__description-text">Body: 98% COTTON - 2% ELASTANE</p>
            <img src="img/products/care-label.jpg" alt="care-label-img" class="single-product__description-label">
          </div>
        </div>
      </div>
    </div>

    <!-- Product Slider -->
    <app-product-slider>
      <h2 class="heading-lg">
        Related Products
        <router-link tag="a" to="/shop" class="btn btn--bordered">Show More</router-link>
      </h2>
    </app-product-slider>

    <!-- Cart Notification -->
    <app-cart-notification :class="{display: notificationShown}"></app-cart-notification>
  </div>
</template>

<script>
import Header from '../components/Header.vue';
import Footer from '../components/Footer.vue';
import ProductSlider from '../components/ProductSlider.vue';
import Subscription from '../components/Subscription.vue';
import CartNotification from '../components/CartNotification.vue';
import Swiper from 'swiper';
import axios from 'axios';

export default {
  data() {
    return {
      productId: parseInt(this.$route.params.id),
      products: [],
      product: '',
      notificationShown: false,
      quantity: 1
    }
  },
  components: {
    'app-header': Header,
    'app-product-slider': ProductSlider,
    'app-cart-notification': CartNotification
  },
  watch: {
    '$route'(to, from) {
      this.productId = parseInt(to.params.id);
      this.quantity = 1;
    },
    productId: function() {
      this.findMatchingProduct();
    }
  },
  methods: {
    loadProducts() {
      axios.get('products.json')
        .then((response) => {
          this.products = response.data.products;
        })
        .then(() => {
          this.findMatchingProduct();
        })
        .then(() => {
          this.initImageSlider();
        });
    },
    findMatchingProduct() {
      this.product = this.products.filter((prod) => {
        return this.productId === prod.id;
      });
    },
    initImageSlider() {
      let galleryThumbs = new Swiper('.gallery-thumbs', {
        spaceBetween: 10,
        slidesPerView: 3,
        watchSlidesVisibility: true,
        watchSlidesProgress: true,
        direction: 'vertical',
        noSwiping: true,
        allowSlidePrev: false,
        allowSlideNext: false
      });

      new Swiper('.gallery-top', {
        fade: true,
        effect: 'fade',
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        thumbs: {
          swiper: galleryThumbs
        },
        scrollbar: {
          el: '.swiper-scrollbar',
          hide: false,
        },
      });
    },
    addToCart() {
      const shoppingCartObj = {
        product: this.product[0],
        quantity: this.quantity
      }
      this.$store.dispatch('add', shoppingCartObj);
      this.showCartNotification();
    },
    showCartNotification() {
      this.notificationShown = true;
      setTimeout(() => {
        this.notificationShown = false;
      }, 2000);
    },
    changeQuantity(e) {
      if (e.target.innerHTML === '+') {
        if (this.quantity >= 1 && this.quantity < 10) {
          this.quantity++;
        }
      } else if (e.target.innerHTML === '-') {
        if (this.quantity > 1 && this.quantity <= 10) {
          this.quantity--;
        }
      }
    }
  },
  mounted() {
    this.loadProducts();
  }
  
}
</script>